import { useEffect, useState } from "react";
import useClientes from "../../hooks/useClientes";

const hojaVacia = {
  total: 0,
  queda: 0,
};

export const usePedidos = () => {
  const { clientes } = useClientes(true);
  const [hojaPedido, setHojaPedido] = useState({ ...hojaVacia });
  const [pedidos, setPedidos] = useState([]);

  useEffect(() => {
    if (localStorage.getItem("hojaPedido")) {
      setHojaPedido({ ...hojaPedido, ...JSON.parse(localStorage.getItem("hojaPedido")) });
    }
    if (localStorage.getItem("pedidos")) {
      setPedidos([...pedidos, ...JSON.parse(localStorage.getItem("pedidos"))]);
    }
  }, []);

  const leerDatos = (pedidoString) => {
    let total=0;
    let arrayPedido = pedidoString.trim().toLowerCase().split("+");
    arrayPedido.forEach((pedido) => {
      const posM = pedido.indexOf("m");
      const posH = pedido.indexOf("h");
      if (posM !== -1) total += pedido.substring(0, posM) * 1;
      if (posH !== -1) total += pedido.substring(0, posH) * 1;
    });
    return total;
  };

  const handlePedido = (i, e) => {
    const nuevoValor = leerDatos(e.target.value)
    let aux = 0;
    aux = hojaPedido.queda * 1 - nuevoValor;
    if (pedidos[i]) {
      aux += pedidos[i].total;
    }
    if (aux >= 0) {
      pedidos[i] = {
        pedidoString: e.target.value,
        total: nuevoValor
      };
      setHojaPedido({...hojaPedido, queda: aux});
      localStorage.setItem("pedidos", JSON.stringify(pedidos));
      localStorage.setItem("hojaPedido", JSON.stringify({...hojaPedido, queda: aux}));
    } else {
      e.target.value = pedidos[i]?.pedidoString || 0;
    }
  };

  const handleTotal = (e) => {
    if (e.target.value * 1 >= 0) {
      const aux = e.target.value * 1;
      const queda = aux - hojaPedido.total + hojaPedido.queda;
      setHojaPedido({ ...hojaPedido, total: aux, queda: queda});
      localStorage.setItem("hojaPedido", JSON.stringify(hojaPedido));
    }
  };

  const limpiarHoja = () => {
    localStorage.removeItem("hojaPedido");
    localStorage.removeItem("pedidos");
    setHojaPedido({...hojaVacia})
    setPedidos([])
  };

  return {
    hojaPedido,
    pedidos,
    clientes,
    handlePedido,
    handleTotal,
    limpiarHoja,
  };
};
