import {
  Button,
  FormControlLabel,
  Switch,
  TextField,
} from "@material-ui/core";
import React from "react";
import ModalForm from "../../../components/ModalForm";
import useForm from "../../../hooks/useForm";
import { postCliente } from "../../../services/clienteService";
import useOpen from "../hooks/useOpen";

const formVacio = {
  nombre: "",
  precio: "",
  saldo: "",
  estado: true,
};

const ModalCliente = ({ addCliente }) => {
  const { form, handleChange, limpiarForm } = useForm({ ...formVacio });
  const { open, handleOpen } = useOpen();

  const handleSubmit = (e) => {
    e.preventDefault();
    postCliente({ ...form }).then((rpta) => {
      if (rpta.request.status === 201) {
        handleOpen();
        limpiarForm();
        addCliente(rpta.data);
      }
    });
  };

  return (
    <>
      <div>
        <Button variant="contained" color="primary" onClick={handleOpen}>
          Nuevo
        </Button>
      </div>
      <ModalForm
        open={open}
        handleOpen={handleOpen}
        handleSubmit={handleSubmit}
      >
        <TextField
          label="Nombre"
          value={form.nombre}
          onChange={handleChange("nombre")}
        />
        <TextField
          label="Camal"
          value={form.camal}
          onChange={handleChange("camal")}
        />
        {/* <TextField
          label="Precio"
          type="number"
          value={form.precio}
          onChange={handleChange("precio")}
          InputProps={{
            startAdornment: <InputAdornment position="start">+</InputAdornment>,
          }}
        />
        <TextField
          label="Saldo"
          type="number"
          value={form.saldo}
          onChange={handleChange("saldo")}
          InputProps={{
            startAdornment: (
              <InputAdornment position="start">S/.</InputAdornment>
            ),
          }}
        /> */}
        <FormControlLabel
          control={
            <Switch
              checked={form.estado}
              onChange={handleChange("estado")}
              name="estado"
              color="primary"
            />
          }
          label={form.estado ? "Activo" : "Desactivo"}
        />
        <Button variant="contained" color="primary" type="submit">
          Guardar
        </Button>
        <Button
          variant="outlined"
          color="primary"
          onClick={() => {
            limpiarForm();
            handleOpen();
          }}
        >
          Cancelar
        </Button>
      </ModalForm>
    </>
  );
};

export default ModalCliente;
