import {
  Table,
  TableContainer,
  TableHead,
  TableRow,
  TableCell,
  TextField,
  TableBody,
  Button,
} from "@material-ui/core";
import React from "react";
import { useRef } from "react";
import ReactToPrint from "react-to-print";
import { usePedidos } from "./hooks";
import { useStyles } from "./styles";

const HojaPedido = () => {
  const {
    hojaPedido,
    pedidos,
    clientes,
    handlePedido,
    handleTotal,
    limpiarHoja,
  } = usePedidos();

  const classes = useStyles();

  const hojaRef = useRef();

  return (
    <div>
      <div className={classes.containerButtons}>
        <TextField
          label="Total"
          value={hojaPedido.total !== 0 ? hojaPedido.total : ""}
          placeholder="0"
          onChange={handleTotal}
          type="number"
        />
        <TextField
          label="Queda"
          value={hojaPedido.queda !== 0 ? hojaPedido.queda : ""}
          type="number"
        />
        <Button variant="contained" color="primary" onClick={limpiarHoja}>
          Limpiar Hoja
        </Button>
        <ReactToPrint
          trigger={() => (
            <Button variant="contained" color="primary">
              Imprimir
            </Button>
          )}
          content={() => hojaRef.current}
        />
        {/* <a href={`https://wa.me/51948901251?document=${pedidos}`} target="_blank">Enviar</a> */}
      </div>
      <TableContainer ref={hojaRef} className={classes.table}>
        <Table size="small" aria-label="a dense table">
          <TableHead>
            <TableRow>
              {["Clientes", "NÂ° de jabas", "Camal"].map((cellHead) => (
                <TableCell key={cellHead}>{cellHead}</TableCell>
              ))}
            </TableRow>
          </TableHead>
          <TableBody>
            {clientes.map((objCliente, i) => (
              <TableRow key={i} className={classes[`row${i%4}`]}>
                <TableCell className={classes.bodyCell}>
                  {objCliente.nombre}
                </TableCell>
                <TableCell className={classes.inputCell}>
                  <TextField
                    size="small"
                    variant="filled"
                    value={
                      pedidos[i]?.pedidoString === undefined ||
                      pedidos[i]?.pedidoString === null
                        ? ""
                        : pedidos[i].pedidoString
                    }
                    onChange={(e) => {
                      handlePedido(i, e);
                    }}
                  />
                </TableCell>
                <TableCell>
                  {objCliente.camal}
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
    </div>
  );
};

export default HojaPedido;
