import React from "react";
import {
  Divider,
  Drawer,
  List,
  ListItem,
  ListItemIcon,
  ListItemText,
  Toolbar,
} from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import  DesktopWindowsIcon  from "@material-ui/icons/DesktopWindows";
import  MoneyIcon  from "@material-ui/icons/AttachMoney";
import PeopleIcon from '@material-ui/icons/People';
import { Link } from "react-router-dom";
import "./styles.css";

const drawerWidth = 240;

const useStyles = makeStyles({
  root: {
    backgroundColor: "red",
  },
  drawer: {
    width: drawerWidth,
    flexShrink: 0,
  },
  drawerPaper: {
    width: drawerWidth,
    padding: "0 10px",
  },
  listItem: {
    borderRadius: 10,
  },
});

const SideBar = () => {
  const classes = useStyles();
  return (
    <Drawer
      variant="permanent"
      className={classes.drawer}
      open
      classes={{
        paper: classes.drawerPaper,
      }}
    >
      <Toolbar />
      <Divider />
      <List>
        {/* <Link to="/">
          <ListItem button className={classes.listItem}>
            <ListItemIcon>
              <DesktopWindowsIcon />
            </ListItemIcon>
            <ListItemText primary="Dashboard" />
          </ListItem>
        </Link> */}
        <Link to="/clientes">
          <ListItem button className={classes.listItem}>
            <ListItemIcon>
              <PeopleIcon />
            </ListItemIcon>
            <ListItemText primary="Clientes" />
          </ListItem>
        </Link>
        {/* <Link to="/productos">
          <ListItem button className={classes.listItem}>
            <ListItemIcon>
              <DesktopWindowsIcon />
            </ListItemIcon>
            <ListItemText primary="Productos" />
          </ListItem>
        </Link>
        <Link to="/pedidos">
          <ListItem button className={classes.listItem}>
            <ListItemIcon>
              <DesktopWindowsIcon />
            </ListItemIcon>
            <ListItemText primary="Pedidos" />
          </ListItem>
        </Link> */}
        <Link to="/">
          <ListItem button className={classes.listItem}>
            <ListItemIcon>
              <DesktopWindowsIcon />
            </ListItemIcon>
            <ListItemText primary="Hoja de Pedidos" />
          </ListItem>
        </Link>
        <Link to="/pagos">
          <ListItem button className={classes.listItem}>
            <ListItemIcon>
              <MoneyIcon />
            </ListItemIcon>
            <ListItemText primary="Pagos" />
          </ListItem>
        </Link>
      </List>
    </Drawer>
  );
};

export default SideBar;
