import { makeStyles } from "@material-ui/core";

export const useStyles = makeStyles((theme) => ({
  containerButtons: {
    display: 'flex',
    gap: "20px"
  },
  table: {
    width: "790px",
  },
  row0: {
    backgroundColor: "#E7ECEF"
  },
  row1: {
    backgroundColor: "#E0F5EB"
  },
  row2: {
    backgroundColor: "#FAF5B2"
  },
  row3: {
    backgroundColor: "#B7D9F5"
  },
  bodyCell: {
    width: "200px",
  },
  inputCell: {
    width: "250px",
    padding: "0",
    margin: "0",
    "& *": {
      padding: "5px",
      margin: "0",
      backgroundColor: "transparent",
    },
    "& .MuiFormControl-root.MuiTextField-root": {
      padding: "0"
    }
  }
}))
