import {
  Button,
  FormControlLabel,
  InputAdornment,
  Switch,
  TextField,
} from "@material-ui/core";
import React from "react";
import ModalForm from "../../../components/ModalForm";
import useForm from "../../../hooks/useForm";
import { putCliente } from "../../../services/clienteService";

const ModalClienteEditar = ({ loadClientes, open, handleOpen, clienteEdit }) => {
  const { form, handleChange } = useForm(null, clienteEdit);

  const handleSubmit = (e) => {
    e.preventDefault();
    putCliente({...form}).then((rpta) => {
      if (rpta.request.status === 200) {
        handleOpen();
        loadClientes()
      }
    });
  };

  return (
    <>
      {form ? (
        <ModalForm
          open={open}
          handleOpen={handleOpen}
          handleSubmit={handleSubmit}
        >
          <TextField
            label="Nombre"
            value={form.nombre}
            onChange={handleChange("nombre")}
          />
          <TextField
          label="Camal"
          value={form.camal}
          onChange={handleChange("camal")}
        />
          {/* <TextField
            label="Precio"
            type="number"
            value={form.precio}
            onChange={handleChange("precio")}
            InputProps={{
              startAdornment: (
                <InputAdornment position="start">+</InputAdornment>
              ),
            }}
          />
          <TextField
            label="Saldo"
            type="number"
            value={form.saldo}
            onChange={handleChange("saldo")}
            InputProps={{
              startAdornment: (
                <InputAdornment position="start">S/.</InputAdornment>
              ),
            }}
          /> */}
          <FormControlLabel
            control={
              <Switch
                checked={form.estado}
                onChange={handleChange("estado")}
                name="estado"
                color="primary"
              />
            }
            label={form.estado ? "Activo" : "Desactivo"}
          />
          <Button variant="contained" color="primary" type="submit">
            Guardar
          </Button>
          <Button
            variant="outlined"
            color="primary"
            onClick={() => {
              handleOpen();
            }}
          >
            Cancelar
          </Button>
        </ModalForm>
      ) : null}
    </>
  );
};

export default ModalClienteEditar;
