import {
    Table,
    TableContainer,
    TableHead,
    TableRow,
    TableCell,
    TextField,
    TableBody,
    Button,
} from "@material-ui/core";
import React, { useState, useEffect } from "react";
import { useRef } from "react";
import ReactToPrint from "react-to-print";
import useClientes from "../../hooks/useClientes";
//   import { usePedidos } from "./hooks";
import { useStyles } from "./styles";

const hojaPagosVacia = {
    importePerla: 0,
    totalPagos: 0,
    diferencia: 0
}

const HojaPagos = () => {
    // const {
    //   hojaPedido,
    //   pedidos,
    //   clientes,
    //   handlePedido,
    //   handleTotal,
    //   limpiarHoja,
    // } = usePedidos();
    const { clientes } = useClientes();
    const [pagos, setPagos] = useState([]);
    const [hojaPagos, setHojaPagos] = useState({...hojaPagosVacia});
    const classes = useStyles();
    const hojaRef = useRef();

    const handleChangePago = (i, e) => {
        const pagosCopy = [...pagos];
        const hojaPagosCopy = { ...hojaPagos };
        pagosCopy[i] = e.target.value;
        const iTotal = pagosCopy.reduce((previousValues, currentValue) => {
            return previousValues += currentValue ? parseFloat(currentValue) : 0
        }
            , 0);
        hojaPagosCopy.totalPagos = iTotal;
        hojaPagosCopy.diferencia = hojaPagosCopy.totalPagos - hojaPagosCopy.importePerla;
        localStorage.setItem("pagos", JSON.stringify(pagosCopy));
        localStorage.setItem("hojaPagos", JSON.stringify(hojaPagosCopy));
        setPagos(pagosCopy);
        setHojaPagos(hojaPagosCopy);
    }

    const handleChangePerla = (e) => {
        const hojaPagosCopy = { ...hojaPagos };
        hojaPagosCopy.importePerla = parseFloat(e.target.value);
        hojaPagosCopy.diferencia = hojaPagosCopy.totalPagos - hojaPagosCopy.importePerla;
        localStorage.setItem("hojaPagos", JSON.stringify(hojaPagosCopy));
        setHojaPagos(hojaPagosCopy);
    }

    const limpiarHoja = () => {
        localStorage.removeItem("pagos");
        localStorage.removeItem("hojaPagos");
        setHojaPagos({ ...hojaPagosVacia })
        setPagos([])
    }

    useEffect(() => {
        if (localStorage.getItem("pagos")) {
            setPagos([...pagos, ...JSON.parse(localStorage.getItem("pagos"))]);
        }
        if (localStorage.getItem("hojaPagos")) {
            setHojaPagos(JSON.parse(localStorage.getItem("hojaPagos")));
        }
    }, []);

    return (
        <div>
            <div className={classes.containerButtons}>
                <TextField
                    label="Total Pagos"
                    value={hojaPagos.totalPagos}
                    type="number"
                />
                <TextField
                    label="Perla"
                    value={hojaPagos.importePerla !== 0 ? hojaPagos.importePerla : ""}
                    onChange={handleChangePerla}
                    type="number"
                />
                <TextField
                    label="Diferencia"
                    value={hojaPagos.diferencia !== 0 ? hojaPagos.diferencia : ""}
                    type="number"
                />
                <Button variant="contained" color="primary" onClick={limpiarHoja}>
                    Limpiar Hoja
                </Button>
                <ReactToPrint
                    trigger={() => (
                        <Button variant="contained" color="primary">
                            Imprimir
                        </Button>
                    )}
                    content={() => hojaRef.current}
                />
                {/* <a href={`https://wa.me/51948901251?document=${pedidos}`} target="_blank">Enviar</a> */}
            </div>
            <TableContainer ref={hojaRef} className={classes.table}>
                <Table size="small" aria-label="a dense table">
                    <TableHead>
                        <TableRow>
                            {["Clientes", "Efectivo", "Banco"].map((cellHead) => (
                                <TableCell key={cellHead}>{cellHead}</TableCell>
                            ))}
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {clientes.map((objCliente, i) => (
                            <TableRow key={i} className={classes[`row${i % 4}`]}>
                                <TableCell className={classes.bodyCell}>
                                    {objCliente.nombre}
                                </TableCell>
                                <TableCell className={classes.inputCell}>
                                    <TextField
                                        size="small"
                                        variant="filled"
                                        type="number"
                                        value={pagos[i] ? pagos[i] : ""}
                                        onChange={(e) => {
                                            handleChangePago(i, e);
                                        }}
                                    />
                                </TableCell>
                                <TableCell className={classes.inputCell}>
                                    <TextField
                                        size="small"
                                        variant="filled"
                                    />
                                </TableCell>
                            </TableRow>
                        ))}
                    </TableBody>
                </Table>
            </TableContainer>
        </div>
    );
};

export default HojaPagos;
