import React from "react";
import {
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
} from "@material-ui/core";

const TablaCliente = ({ clientes, handleOpen, setClienteEdit }) => {
  return (
    <TableContainer>
      <Table size="small" aria-label="a dense table">
        <TableHead>
          <TableRow>
            {["Nombre", "Camal", "Estado"].map((cellHead) => (
              <TableCell key={cellHead}>{cellHead}</TableCell>
            ))}
          </TableRow>
        </TableHead>
        <TableBody>
          {clientes.map((cliente, i) => (
            <TableRow
              key={`row${i}`}
              onClick={() => {
                setClienteEdit({ ...cliente });
                handleOpen();
              }}
            >
              {["nombre", "camal", "estado"].map((column, j) => {
                if (column === "estado") {
                  return (
                    <TableCell key={`column${i}${j}`}>
                      {cliente[column] === true ? "Activo" : "Desactivo"}
                    </TableCell>
                  );
                } else {
                  return (
                    <TableCell key={`column${i}${j}`}>
                      {cliente[column]}
                    </TableCell>
                  );
                }
              })}
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  );
};

export default TablaCliente;
