import axios from 'axios';
import { URL_BACKEND } from '../environments/environment';

export const getClientes = async () => {
  const rpta = await axios.get(`${URL_BACKEND}/cliente`)
  return rpta
}

export const getClientesActive = async () => {
  const rpta = await axios.get(`${URL_BACKEND}/cliente?estado=true`)
  return rpta
}

export const postCliente = async (objCliente) => {
  const rpta = await axios.post(`${URL_BACKEND}/cliente`,
  JSON.stringify(objCliente),
  {headers: { 
    'Content-Type': 'application/json'
  }}
  )
  return rpta;
}

export const putCliente = async (objCliente) => {
  const rpta = await axios.put(`${URL_BACKEND}/cliente/${objCliente.id}`,
  JSON.stringify(objCliente),
  {headers: { 
    'Content-Type': 'application/json'
  }}
  )
  return rpta;
}