import { Card, Modal } from "@material-ui/core";
import React from "react";
import useStyles from "./styles";

const ModalForm = ({ open, handleOpen, handleSubmit, children, titleForm }) => {
  const classes = useStyles();

  return (
    <Modal
      open={open}
      onClose={handleOpen}
      className={classes.modalContainer}
      aria-labelledby="simple-modal-title"
      aria-describedby="simple-modal-description"
    >
      <Card variant="outlined" className={classes.modal}>
        <h2 id="simple-modal-title">{titleForm}</h2>
        <form id="simple-modal-description" className={classes.form} onSubmit={handleSubmit}>
          {children}
        </form>
      </Card>
    </Modal>
  );
};

export default ModalForm;
