import React, { useState } from "react";
import ModalCliente from "./components/ModalCliente";
import useClientes from "../../hooks/useClientes";
import ModalClienteEditar from "./components/ModalClienteEditar";
import TablaCliente from "./components/TablaCliente";
import useOpen from "./hooks/useOpen";

const ClientesView = () => {

  const { clientes, addCliente, loadAllClientes } = useClientes()

  const [clienteEdit, setClienteEdit] = useState(null)

  const { open: openEdit, handleOpen: handleOpenEdit} = useOpen()

  

  return (
    <>
      <ModalCliente addCliente={addCliente}/>
      <ModalClienteEditar open={openEdit} handleOpen={handleOpenEdit} clienteEdit={clienteEdit} loadClientes={loadAllClientes}/>
      <TablaCliente clientes={clientes} handleOpen={handleOpenEdit} setClienteEdit={setClienteEdit}/>
    </>
  );
};

export default ClientesView;
